import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage } from 'gatsby-plugin-image';
import * as styles from './Clients.module.scss';
import { motion } from 'framer-motion';
export const Clients = ()=>{
    const data = useStaticQuery(CLIENTS_LOGO_QUERY);
    return React.createElement("div", {
        className: styles.feed
    }, data.logos.nodes.map((client)=>React.createElement(motion.a, {
            initial: {
                opacity: 0,
                scale: 0.7,
                y: 100
            },
            whileInView: {
                opacity: 1,
                scale: 1,
                y: 0
            },
            viewport: {
                once: true
            },
            transition: {
                duration: 0.5
            },
            title: client.title,
            key: client.id,
            href: client.url,
            target: "_blank",
            rel: "noreferrer"
        }, React.createElement(GatsbyImage, {
            className: styles.logoImg,
            image: client.img.childImageSharp.gatsbyImageData,
            alt: client.title
        }))));
};
const CLIENTS_LOGO_QUERY = graphql`
  query LogoFiles {
    logos: allClientsYaml {
      nodes {
        title
        url
        id
        img {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH)
          }
        }
      }
    }
  }
`;
